import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { Context } from "../../../App";
import Container from "../../../Components/Container";
import { getEnabledKeys } from "../../../Helpers";
import { city, subjectForEmail, bodyForEmail } from "../utils"
import "./index.scss";

export const Influencer = () => {
    const navigate = useNavigate();
    const initialPromotion = 
    [
      {label: "Lifestyle", value: false},
      // {label: "Beauty", value: false},
      {label: "Food", value: false},
      {label: "Fitness", value: false},
      // {label: "Health", value: false}
    ]
    const [loading, setLoading] = useState(false);
    const [username, setusername] = useState("");
    const [firstname, setfirstname] = useState("");
    const [lastname, setlastname] = useState("");
    const [password, setPassword] = useState("");
    const [instaId, setInstaId] = useState("");
    const [followers, setFollowers] = useState("");
    const [currentCity, setCity] = useState("");
    const [referralCode, setReferralCode] = useState("");
    const [cityManually, setCityManually] = useState(false);
    const [otherPromotion, setOtherPromotion] = useState(false);
    const [promotionManually, setPromotionManually] = useState("");
    const [promotion, setpromotion] = useState(initialPromotion);

    const {referral, setReferral} = useContext(Context);

    useEffect(() => {
      if(window.location.search.includes("refer")){
        setReferralCode(window.location.search.split("=")[1])
      }
    } , [])

    const onChangeUsername = (e: React.ChangeEvent<HTMLInputElement>) => {
        setusername(e.target.value);
    }
    const onChangeFirstname = (e: React.ChangeEvent<HTMLInputElement>) => {
      setfirstname(e.target.value);
    }
    const onChangeLastname = (e: React.ChangeEvent<HTMLInputElement>) => {
      setlastname(e.target.value);
    }
    const onChangeReferralCode = (e: React.ChangeEvent<HTMLInputElement>) => {
      setReferralCode(e.target.value);
    }
    const onChangePassword = (e: React.ChangeEvent<HTMLInputElement>) => {
        setPassword(e.target.value);
    }
    const onChangePromotion = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setPromotionManually(e.target.value);
    }
    const onChangeInstaId = (e: React.ChangeEvent<HTMLInputElement>) => {
      if(instaId.includes("@")){
        setInstaId(e.target.value);
      } else {
        setInstaId(`@${e.target.value}`);
      }
    }
    const onChangeFollowers = (e: React.ChangeEvent<HTMLInputElement>) => {
      setFollowers(e.target.value);
    }
    const onChangeCity = (e: React.ChangeEvent<HTMLInputElement>) => {
      setCity(e.target.value);
    }
    const onChangePromotions = (label: string, reset?: boolean) => {
      if(reset){
        setpromotion(promotion);
      } else {
        const newPromotion = promotion.map(item => {
          if (item.label === label) {
            return {...item, value: !item.value}
          }
          return item;
        }
        );
        setpromotion(newPromotion);
      }
    }
    const onRegister = async () => {
        if(otherPromotion) {
          promotionManually.split(",").forEach(item => {
            promotion.push({label: item, value: true});
          });
        }

        try {
          setReferral(instaId);
          setLoading(true);

          await fetch("https://script.google.com/macros/s/AKfycbwSkzWLXLVsdBY9W5eW04s3SyPv3eebShhLwETSBhS-zIoYQyJz_WyeBQhbOpTOzUwY/exec", {
            method: "POST",
            mode: "cors",
            redirect: "follow",
            headers: {
              "Content-Type": "text/plain"
              },
            body: JSON.stringify([username, 
              instaId, 
              followers, 
              currentCity, 
              JSON.stringify(getEnabledKeys(promotion).slice(0, 3)),
              referralCode,
              firstname,
              lastname,
              ],
            )
          });

          let body = bodyForEmail.replace("{username}", `${firstname} ${lastname}`);
          body = body.replace("{referral}", `https://www.nanoflex.uk/register/influencer?refer=${instaId}`);


          await fetch("https://script.google.com/macros/s/AKfycbyF4TGJrM6SnrD7hgbXSk2MIKlxQmgNn0r9D5eBNZm7Rs7rDTXEiZ47sOm0U9ZJtJ_GOQ/exec", {
                method: "POST",
                mode: "cors",
                redirect: "follow",
                headers: {
                    "Content-Type": "text/plain"
                },
                body: JSON.stringify({ senders: username, subject: subjectForEmail, body })
            })
          
          navigate("/register/complete");

          // await fetch("https://sheetdb.io/api/v1/nly75yraygdbv", {
          //   method: "POST",
          //   headers: {
          //     "Content-Type": "application/json"
          //     },
          //     body: JSON.stringify({
          //       email: username,
          //       // password,
          //       instagramID: instaId,
          //       followers,
          //       city: currentCity,
          //       promotion: getEnabledKeys(promotion)
          //       })
          // });
        }
        catch (error) {
          console.log(error);
        }
    }

    return (
        <div className="contentSect">
            <Container>
                <div id="loginform">
                    <div className="headSect textCenter">
                      <h2>Register</h2>
                    </div>
                    <p>Fill in the form and we'll contact you about paid posts and stories</p>
                    <div className="form-row">
                      <label>Email</label>
                      <input type="text" value={username} onChange={onChangeUsername} placeholder="Please enter your email address"/>
                    </div> 
                    <div className="form-row">
                      <label>First Name</label>
                      <input type="text" value={firstname} onChange={onChangeFirstname} placeholder="Please enter your first name"/>
                    </div> 
                    <div className="form-row">
                      <label>Last Name</label>
                      <input type="text" value={lastname} onChange={onChangeLastname} placeholder="Please enter your last name"/>
                    </div> 
                    {/* <div className="form-row">
                      <label>Password</label>
                      <input type="password" value={password} onChange={onChangePassword} placeholder="Please enter your password"/>
                    </div>  */}
                    <div className="form-row">
                      <label>Instagram Name</label>
                      <input type="text" value={instaId} onChange={onChangeInstaId} placeholder="Please enter your instagram name"/>
                    </div> 
                    <div className="form-row">
                      <label>Total Followers</label>
                      <input type="number" value={followers} onChange={onChangeFollowers} placeholder="Please enter your total followers"/>
                    </div> 
                    <div className="form-row mb10">
                      <label>City</label>
                      {!cityManually && <select className="mb15" value={currentCity} onChange={onChangeCity as any}>
                          {[{label: "Please select your city", value: ""},...city].map((option: any) => (
                              <option key={option.value} value={option.value}>{option.label}</option>
                          ))}
                      </select>}
                      <div className="flex alignItemsCenter checkbox alignSelfStart">
                          <input checked={cityManually} onChange={() => {setCityManually(!cityManually); onChangeCity({target: {value: ""}} as any)}} type="checkbox"/>
                          <label className="optionLabel">Enter other cities</label>
                      </div>
                      {cityManually && <input type="text" value={currentCity} onChange={onChangeCity} placeholder="Please enter your city"/>}
                    </div>
                    <div className="form-row">
                      <label>Personal Interests</label>
                      {promotion.map((option: any) => (
                        <div className="flex alignItemsCenter checkbox alignSelfStart" key={option.label}>
                          <input id={option.label} checked={option.value} onChange={() => onChangePromotions(option.label)} type="checkbox"/>
                          <label htmlFor={option.label} className="optionLabel" >{option.label}</label>
                        </div>
                      ))}
                       <div className="flex alignItemsCenter checkbox alignSelfStart">
                          <input checked={otherPromotion} onChange={() => {setOtherPromotion(!otherPromotion); onChangePromotions("", true)}} type="checkbox"/>
                          <label className="optionLabel">Enter other interests</label>
                      </div>
                      {otherPromotion && <textarea rows={2} value={promotionManually} onChange={onChangePromotion} placeholder="Please enter your personal interests seperated by a comma"/>}
                    </div>
                    <p className="attention">Please provide a maximum of 3 interests</p>
                    <div className="form-row">
                      <label>Referral Code</label>
                      <input type="text" value={referralCode} onChange={onChangeReferralCode} placeholder="Please enter a referral code if available"/>
                    </div> 
                    <div id="button" className="form-row">
                      <button onClick={onRegister} disabled={loading} >Register</button>
                    </div>
                </div>
            </Container>
        </div>
    )
}