import React, { useState } from "react";
import { Link } from "react-router-dom";
import Container from "../../Components/Container";
import "./index.scss";

export const Login = () => {
    const [username, setusername] = useState("");
    const [password, setPassword] = useState("");

    const onChangeUsername = (e: React.ChangeEvent<HTMLInputElement>) => {
        setusername(e.target.value);
    }
    const onChangePassword = (e: React.ChangeEvent<HTMLInputElement>) => {
        setPassword(e.target.value);
    }
    const onLogin = () => {
        console.log(username, password);
    }

    return (
        <div className="contentSect">
            <Container>
                <div id="loginform">
                    <div className="headSect textCenter">
                          <h2>Login</h2>
                    </div>
                    <div className="form-row">
                      <label>Username</label>
                      <input type="text" value={username} onChange={onChangeUsername} placeholder="Please enter your username"/>
                    </div> 
                    <div className="form-row">
                      <label>Password</label>
                      <input type="password" value={password} onChange={onChangePassword} placeholder="Please enter your password"/>
                    </div> 
                    <div id="button" className="form-row">
                      <button onClick={onLogin}>Login</button>
                    </div>
                    <div id="alternativeLogin">
                      <label>Or sign in with:</label>
                      <div id="iconGroup">
                        <a href="#" id="facebookIcon"><img src="https://cdn-icons-png.flaticon.com/512/733/733547.png"/></a>
                        <a href="#" id="twitterIcon"><img src="https://cdn-icons-png.flaticon.com/512/174/174855.png"/></a>
                        <a href="#" id="googleIcon"><img src="https://cdn-icons-png.flaticon.com/512/300/300221.png"/></a>
                      </div>
                    </div>
                    <div className="textCenter">
                      <p>Don't have an account with us?</p>
                      <Link className="registerLink" to="/register">Register</Link>
                    </div>
                </div>
            </Container>
        </div>
    )
}