import React, { useContext } from "react";
import { Context } from "../../../App";
import Container from "../../../Components/Container";
import "./index.scss";

export const RegistrationComplete = () => {
    const {referral, setReferral} = useContext(Context);
    const [copied, setCopied] = React.useState(false);

    const onCopyLink = () => {
        navigator.clipboard.writeText(`https://www.nanoflex.uk/register/influencer?refer=${referral}`);
        setCopied(true)
    }

    return (
        <div className="contentSect jobDetails">
            <Container>
                <img className="imageContent" src="/thankyou.png" alt="thanks" />
                <h4 className="textContent">For registering with us, we will get in touch with you soon...</h4>
                {referral && 
                <>
                     <p className="referralContent">Your personal referral link is ready</p>
                <p className="referralText">Now all you have to do is send it to your friends and win exciting rewards.</p>
                <input className="referralInput" type="text" value="CLICK TO COPY THE LINK" readOnly onClick={onCopyLink}/>
                {copied && <p className="copyText">Copied!</p>}
                </>
               }
            </Container>
        </div>
    )
}