import React from "react";
import { Link } from "react-router-dom";
import "./index.scss";

export type IProps = {
  title: string;
  description: string;
  url?: string;
  cardImage?: string;
  children?: React.ReactNode;
  buttonText?: string 
}

const Card = (props: IProps) => {

  const {title, description, url, cardImage, children, buttonText = "Read More"} = props;

  return (

    <div className="card">
      <div className="cardWrapper">
          {cardImage && (<div className="cardImg">
              <img src={cardImage} alt={title} />
          </div>)}
          <div className="cardDetails">
              <h4>{title}</h4>
              <p>{description}</p>
              {url && <Link to={url}>{buttonText}</Link>}
              {children}
          </div>
      </div>
  </div>
  )
}

export default Card;  