import React, { useState } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom"
import { Home } from "../Pages/Home";
import { Login } from "../Pages/Login";
import { Email } from "../Pages/Email";
import Header from "../Components/Header";
import { Register } from "../Pages/Register";
import { Footer } from "../Components/Footer";
import { JobListing } from "../Pages/JobListing";
import { JobDetails } from "../Pages/JobDetails";
import { Promoter } from "../Pages/Register/Promoter";
import { Influencer } from "../Pages/Register/Influencer";
import { Influencers } from "../Pages/Influencers";
import { RegistrationComplete } from "../Pages/Register/Complete";
import "./index.scss"

export const Context = React.createContext({
  referral: "",
  setReferral: (code: string) => {},
  email: "",
  setEmail: (email: string) => {},
});

export default function App() {
  const [referral, setReferral] = useState("");
  const [email, setEmail] = useState("");
  const value = { email, setEmail, referral, setReferral };
  return (
    <BrowserRouter>
      <Header />
      <Context.Provider value={value as any}>
      <Routes>
        <Route path="*" element={<Home />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/job-listing" element={<JobListing />} />
        <Route path="/reinforce-union" element={<Influencers />} />
        <Route path="/job-details/:id" element={<JobDetails />} />
        <Route path="/register/promoter" element={<Promoter />} />
        <Route path="/register/influencer" element={<Influencer />} />
        <Route path="/reinforce-union/email" element={<Email />} />
        <Route path="/register/complete" element={<RegistrationComplete />} />
      </Routes>
      </Context.Provider>
      <Footer />
    </BrowserRouter>
  );
}
