import React, { useState } from "react";
import Container from "../../../Components/Container";
import { productTypes } from "../utils"
import "./index.scss";

export const Promoter = () => {
    const [username, setusername] = useState("");
    const [firstname, setfirstname] = useState("");
    const [lastname, setlastname] = useState("");
    const [password, setPassword] = useState("");
    const [companyName, setCompanyName] = useState("");
    const [companyUrl, setCompanyUrl] = useState("");
    const [industry, setIndustry] = useState("");
    const [info, setInfo] = useState("");
    const [industryManually, setIndustryManually] = useState(false);

    const onChangeUsername = (e: React.ChangeEvent<HTMLInputElement>) => {
        setusername(e.target.value);
    }
    const onChangeFirstname = (e: React.ChangeEvent<HTMLInputElement>) => {
      setfirstname(e.target.value);
    }
    const onChangeLastname = (e: React.ChangeEvent<HTMLInputElement>) => {
      setlastname(e.target.value);
    }
    const onChangePassword = (e: React.ChangeEvent<HTMLInputElement>) => {
        setPassword(e.target.value);
    }
    const onChangeCompanyName = (e: React.ChangeEvent<HTMLInputElement>) => {
      setCompanyName(e.target.value);
    }
    const onChangeCompanyUrl = (e: React.ChangeEvent<HTMLInputElement>) => {
      setCompanyUrl(e.target.value);
    }
    const onChangeIndustry = (e: React.ChangeEvent<HTMLInputElement>) => {
      setIndustry(e.target.value);
    }
    const onChangeInfo = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      setInfo(e.target.value);
    }
    const onRegister = async () => {
        try {
          await fetch("https://script.google.com/macros/s/AKfycbx4Y3vh1Z6Yqog8_meJtSJzYl6dFStjpcjDjT0x1jFDKK9Go9pmpNCwMzTMsPs2zJbFdQ/exec", {
            method: "POST",
            mode: "cors",
            redirect: "follow",
            headers: {
              "Content-Type": "text/plain"
              },
            body: JSON.stringify([
              username,
              // password,
              companyName,
              companyUrl,
              industry,
              info,
              firstname,
              lastname
            ])
          })
          window.location.href = "/register/complete";
        } catch (error) {
          console.log(error);
        }
    }

    return (
        <div className="contentSect">
            <Container>
                <div id="loginform">
                    <div className="headSect textCenter">
                      <h2>Register</h2>
                    </div>
                    <p>Fill in the form and we'll contact you with an offer for how we can help grow your business</p>
                    <div className="form-row">
                      <label>Company Email</label>
                      <input type="text" value={username} onChange={onChangeUsername} placeholder="Please enter your email address"/>
                    </div> 
                    <div className="form-row">
                      <label>First Name</label>
                      <input type="text" value={firstname} onChange={onChangeFirstname} placeholder="Please enter your first name"/>
                    </div> 
                    <div className="form-row">
                      <label>Last Name</label>
                      <input type="text" value={lastname} onChange={onChangeLastname} placeholder="Please enter your last name"/>
                    </div> 
                    {/* <div className="form-row">
                      <label>Password</label>
                      <input type="password" value={password} onChange={onChangePassword} placeholder="Please enter your password"/>
                    </div>  */}
                    <div className="form-row">
                      <label>Company Name</label>
                      <input type="text" value={companyName} onChange={onChangeCompanyName} placeholder="Please enter your company name"/>
                    </div> 
                    <div className="form-row">
                      <label>Company Url</label>
                      <input type="text" value={companyUrl} onChange={onChangeCompanyUrl} placeholder="Please enter your company url"/>
                    </div> 
                    <div className="form-row mb10">
                      <label>Industry</label>
                      {!industryManually && <select className="mb15" value={industry} onChange={onChangeIndustry as any}>
                          {[{label: "Please select your industry", value: ""},...productTypes].map((option: any) => (
                              <option key={option.value} value={option.value}>{option.label}</option>
                          ))}
                      </select>}
                      <div className="flex alignItemsCenter checkbox alignSelfStart">
                          <input checked={industryManually} onChange={() => {setIndustryManually(!industryManually); onChangeIndustry({target: {value: ""}} as any)}} type="checkbox"/>
                          <label className="optionLabel">Enter other industries</label>
                      </div>
                      {industryManually && <input type="text" value={industry} onChange={onChangeIndustry} placeholder="Please enter your industry"/>}
                    </div>
                    <div className="form-row">
                      <label>More Information</label>
                      <textarea rows={3} value={info} onChange={onChangeInfo} placeholder="Give us some information about what you want to promote, markets you want to hit, and problems you want to solve"/>
                    </div>
                    <div id="button" className="form-row">
                      <button onClick={onRegister}>Register</button>
                    </div>
                </div>
            </Container>
        </div>
    )
}