import React, {useState} from "react";
import { Link } from "react-router-dom";
import Container from "../Container";
import "./header.scss";
import menu from "./headerMenu";
import { joinClasses } from "../../utils/joinClasses";

const Header = () => {

    const logoArea = (
        <Link className="logo" to="/" >
            <img src="/logo.png" alt="Nanoflex" />
        </Link>
    )

    const renderedLinks = menu.map(({label, to, href, className}, index)=> {
        if(to) {
            return <Link to={to} key={`headerMenuItem-${index}`} className={className}>{label}</Link>
        } else if(href) {
            return <a href={href} key={`headerMenuItem-${index}`} className={className}>{label}</a>
        }
    })
    
    const renderedHeaderMenu = renderedLinks.map((headerMenuItem, index) => 
        <li key={`headerMenuItems-${index}`} onClick={()=>toggleMenuOpen(false)}>{headerMenuItem}</li>
    )

    const [openMenu, toggleMenuOpen] = useState(false);

    
    return (
        <>
            <header className="topBar">
                <Container className="flex alignItemsCenter justifyContentBetween">
                    <div className="logoSect">
                        {logoArea}
                    </div>
                    <div className="headerMenu">
                        <div className="menu-mobile show-sm textRight">
                            <a role="button" onClick={(e)=> {toggleMenuOpen(!openMenu); e.preventDefault()}} id="openMobileMenu">
                                <svg className="svgIcon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 25 25">
                                    <path fill="#33d1cc" d="M0,4 C0,2.8954305 0.889763236,2 2.00359486,2 L22.9964051,2 C24.10296,2 25,2.88772964 25,4 C25,5.1045695 24.1102368,6 22.9964051,6 L2.00359486,6 C0.897039974,6 0,5.11227036 0,4 L0,4 Z M0,12 C0,10.8954305 0.889763236,10 2.00359486,10 L22.9964051,10 C24.10296,10 25,10.8877296 25,12 C25,13.1045695 24.1102368,14 22.9964051,14 L2.00359486,14 C0.897039974,14 0,13.1122704 0,12 L0,12 Z M0,20 C0,18.8954305 0.889763236,18 2.00359486,18 L22.9964051,18 C24.10296,18 25,18.8877296 25,20 C25,21.1045695 24.1102368,22 22.9964051,22 L2.00359486,22 C0.897039974,22 0,21.1122704 0,20 L0,20 Z" id="Hamburger"></path>
                                </svg>
                            </a>
                        </div>
                        <div className={joinClasses("siteNav", openMenu && "showNav")}>
                            <span onClick={()=>toggleMenuOpen(!openMenu)}  role="button" id="closeMenu" className="show-sm">
                                <svg className="svgIcon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 17 17">
                                    <path fill="#fff" d="M9.207 8.5l6.646 6.646-0.707 0.707-6.646-6.646-6.646 6.646-0.707-0.707 6.646-6.646-6.647-6.646 0.707-0.707 6.647 6.646 6.646-6.646 0.707 0.707-6.646 6.646z"></path>
                                </svg>
                            </span>
                            <ul className="navBar alignItemsCenter justifyContendEnd">
                                {renderedHeaderMenu}
                            </ul>
                            
                        </div>
                    </div>
                </Container>
            </header>
            <span role="button" onClick={()=>toggleMenuOpen(!openMenu)} className={joinClasses("overLay", openMenu && "fixed")}></span>
        </>
    )
}

export default Header;
