import React, { useEffect, useState, useContext } from "react";
import DataTable from 'react-data-table-component';
import { useNavigate } from "react-router-dom";
import Container from "../../Components/Container";
import { Context } from "../../App";


const columns = [
    {
        name: 'Email',
        selector: (row: any) => row.email,
    },
    {
        name: 'First Name',
        selector: (row: any) => row.firstname,
    },
    {
        name: 'Last Name',
        selector: (row: any) => row.lastname,
    },
    {
        name: 'Instagram ID',
        selector: (row: any) => row.instagramID,
    },
    {
        name: 'Total Followers',
        selector: (row: any) => row.followers,
    },
    {
        name: 'Current City',
        selector: (row: any) => row.city,
    },
    {
        name: 'Interests',
        selector: (row: any) => row.promotion,
    },
    {
        name: 'Referral',
        selector: (row: any) => row.referral,
    },
];
const paginationComponentOptions = {
    selectAllRowsItem: false,
    noRowsPerPage: true,
};


export const Influencers = () => {
    const navigate = useNavigate();
    const [response, setResponse] = useState([]);
    const [totalRows, setTotalRows] = useState(0);
    const [pageNumber, setPageNumber] = useState(1);
    const [loading, setLoading] = useState(false);
    const [selectedRows, setSelectedRows] = useState([]);

    const {email, setEmail} = useContext(Context);

    const getInfluencers = async (pageNumber: number) => {
        setLoading(true)
        const data = await fetch("https://script.google.com/macros/s/AKfycbwSkzWLXLVsdBY9W5eW04s3SyPv3eebShhLwETSBhS-zIoYQyJz_WyeBQhbOpTOzUwY/exec?pageNumber="+pageNumber);
        const json = await data.json();
        setResponse(response.concat(json.data));
        setTotalRows(json.total);
        setLoading(false)
    }

    useEffect(() => {
        getInfluencers(1);
    } , []);

    const onSendEmail = () => {
        const selected = selectedRows.map((row: any) => row.email);
        const emails = selected.join(",");
        setEmail(emails);
        navigate("/reinforce-union/email");
    }

    const currentResponse = pageNumber === 1 ? response.slice(0, 10) : response.slice((pageNumber - 1) * 10, pageNumber * 10);
    
    return (
        <div className="contentSect jobDetails">
            <Container>
                <div className="headSect">
                    <h1 className="mb15">Influencers</h1>
                    <DataTable
                        columns={columns}
                        data={currentResponse}
                        selectableRows
                        pagination
                        paginationServer
                        progressPending={loading}
                        paginationTotalRows={totalRows}
                        paginationComponentOptions={paginationComponentOptions}
                        onSelectedRowsChange={(rows: any) => {
                            setSelectedRows(rows.selectedRows);
                        }}
                        onChangePage={(page) => {
                            setPageNumber(page);
                            if(response.length < totalRows) {
                                getInfluencers(page);
                            }
                        }}
                    />
                </div>
                <div id="button" className="form-row">
                      <button onClick={onSendEmail}>Send Email</button>
                </div>
            </Container>
        </div>
    )
}