import React, { useState, useEffect, useContext } from "react";
import Container from "../../Components/Container";
import { Context } from "../../App";
import "./index.scss";

export const Email = () => {
    const [body, setBody] = useState("");
    const [emailId, setEmail] = useState("");
    const [subject, setSubject] = useState("");
    const [loading, setLoading] = useState(false);

    const { email } = useContext(Context);

    useEffect(() => {
        if(email) {
            setEmail(email);
        }
    } , []);

    const onChangeEmail = (e: React.ChangeEvent<HTMLInputElement>) => {
        setEmail(e.target.value);
    }
    const onChangeSubject = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSubject(e.target.value);
    }
    const onChangeBody = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setBody(e.target.value);
    }

    const onSend = async () => {
        setLoading(true);
        try {
            await fetch("https://script.google.com/macros/s/AKfycbyF4TGJrM6SnrD7hgbXSk2MIKlxQmgNn0r9D5eBNZm7Rs7rDTXEiZ47sOm0U9ZJtJ_GOQ/exec", {
                method: "POST",
                // mode: "cors",
                redirect: "follow",
                headers: {
                    "Content-Type": "text/plain"
                },
                body: JSON.stringify({ senders: emailId, subject: subject, body: body })
            })
        } catch (error) {
            console.log(error);
        }
        setLoading(false);
    }

    return (
        <div className="contentSect jobDetails">
            <Container>
                <div className="formSect">
                    <div className="headSect">
                        <h1 className="mb15">Send Email</h1>
                    </div>
                    <div className="form-row">
                      <label>Recipients Email Address</label>
                      <input type="text" value={emailId} onChange={onChangeEmail} placeholder="Enter the recipients email address"/>
                    </div> 
                    <div className="form-row">
                      <label>Subject</label>
                      <input type="text" value={subject} onChange={onChangeSubject} placeholder="Enter the subject"/>
                    </div> 
                    <div className="form-row">
                      <label>Body</label>
                      <textarea rows={4} value={body} onChange={onChangeBody} placeholder="Enter the body, incase new line is needed please enter new line"/>
                    </div> 
                    <div id="button" className="form-row">
                      <button onClick={onSend}>Send Email</button>
                    </div>
                    {loading && <p>Sending Email</p>}
                </div>
            </Container>
        </div>
    )
}
