import React from "react";
import { joinClasses } from "../../utils/joinClasses";
import "./index.scss";

export type IProps = {
  columnCount?: 1 | 2;
  children?: React.ReactNode
}

const CardsContainer = ({columnCount= 1, children} : IProps) => 
  (
    <div 
      className={joinClasses("cardsContainer", columnCount === 2 && "twoColumn" )}
    >{children}</div>
  );

export default CardsContainer;