import React from "react";
import Container from "../Container";
import { Link } from "react-router-dom";
import "./index.scss";

export type IProps = {
  headingText?: string;
  subHeadingText?: string;
  descriptionText?: string;
  businessUrl?: string;
  influencerUrl?: string;
  businessLabel?: string;
  influencerLabel?: string;
  imageUrl?: string;
  bgColor?: string;
}

const Banner = ({
  headingText, 
  subHeadingText, 
  descriptionText, 
  businessUrl, 
  influencerUrl, 
  businessLabel, 
  influencerLabel, 
  imageUrl, 
  bgColor} : IProps) => {
  return(
    <div className="bannerSect flex alignItemsCenter" style={{backgroundImage: `url("${imageUrl}")`, backgroundColor: bgColor}}>
      <Container>
        <div className="bannerContents">
          {headingText && <h1>{headingText}</h1>}
          {subHeadingText && <p>{subHeadingText}</p>}
          {descriptionText && <p className="description">{descriptionText}</p>}
          <div className="flex justifyContentBetween">
            {(businessUrl && businessLabel) && <Link to={businessUrl} className="btn btnPrimary" >{businessLabel}</Link> }
            {(influencerUrl && influencerLabel) && <Link to={influencerUrl} className="btn btnSecondary" >{influencerLabel}</Link> }
          </div>
        </div>
      </Container>
    </div>
  )
}

export default Banner;